<template>
  <div class="hello" >
    <div  @click="toggleActive"> 
      <img class="base" src="https://img-np.xkeshi.cn/dev/crm/1f/08/8243bb1371ce6e155c09260b7c3d.jpeg" alt="">
    </div>
    <div>
    <ml-i-view v-model="imgShow" :url="url" :scale="3"></ml-i-view>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      url: '',
      imgShow: false
    }
  },
  methods: {
    toggleActive() {
      this.imgShow = !this.imgShow
      if(this.imgShow) {
        this.url = 'https://img-np.xkeshi.cn/dev/crm/1f/08/8243bb1371ce6e155c09260b7c3d.jpeg'
      }else {
        this.url = ''
      }
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {

}
.base {
  width: 325px;
}

</style>



